import { AnalyticsUserBackIcon, AnalyzeExportIcon } from "@/assets/AiBotAssets"

const UserDetail = ({ setShowUserDetail, setShowModal }) => {
    return (
        <div className="createbot-modal createbot-new-background modal-scroll analytics-user-detail" onClick={() => setShowUserDetail(false)}>
          <div className="call-api-container" onClick={e => e.stopPropagation()}>
            <div className="user-detail-header">
                <div className="header-left">
                    <AnalyticsUserBackIcon setShowUserDetail={setShowUserDetail} />
                    <span>John Cena</span>
                </div>
                <div className="header-right">
                    <span className="triggered-at">Triggered At</span>
                    <span className="date-time">18 Aug 2021, 12:30 PM</span>
                </div>
            </div>
            <div className="user-detail-body">
                <p className="mb-15">All Responded Node:</p>
                <div className="user-detail-body-inner">
                    <div className="detail-card">
                        <span className="card-name">Notification</span>
                        <span className="card-status not-delivered">Not Delivered</span>
                        <AnalyzeExportIcon setShowModal={setShowModal} setShowUserDetail={setShowUserDetail} />
                    </div>
                    <div className="detail-card">
                        <span className="card-name">Condition</span>
                        <span className="card-status delivered"> Delivered</span>
                        <AnalyzeExportIcon setShowModal={setShowModal} setShowUserDetail={setShowUserDetail} />
                    </div>
                </div>
            </div>
          </div>
        </div>
      )
}

export default UserDetail