import {
  AnalyzeCalenderIcon,
  ResponseEmailIcon,
  SentEmailIcon,
} from "@/assets/AiBotAssets";
import { useEffect, useState } from "react";
import { axiosGet } from "@/services/axiosService";
import moment from "moment";
import SmallLoader from "@/components/loaders/SmallLoader";
import Environment from "@/constants/Environment";

const AnalyticsDetailedModal = ({ setShowModal, answerId, node }) => {
  const [data, setData] = useState<any>();
  const [loader, setLoader] = useState<boolean>(true);
  const [remainingTime, setRemainingTime] = useState("");

  useEffect(() => {
    if (!data) return;
    if (!node?.nodeTitle.includes("delayAction")) return;

    const { time, timeFormat, createdAt } = data;
    const createdDate = moment(createdAt.$date);

    let calculatedEndDate;
    switch (timeFormat) {
      case "days":
        calculatedEndDate = createdDate.add(time, "days");
        break;
      case "hours":
        calculatedEndDate = createdDate.add(time, "hours");
        break;
      case "minutes":
        calculatedEndDate = createdDate.add(time, "minutes");
        break;
      case "seconds":
        calculatedEndDate = createdDate.add(time, "seconds");
        break;
      default:
        calculatedEndDate = createdDate;
        break;
    }
    const calculateRemainingTime = () => {
      const now = moment();
      const duration = moment.duration(calculatedEndDate.diff(now));

      if (duration.asMilliseconds() <= 0) return;
      else {
        const days = duration.days();
        const hours = duration.hours();
        const minutes = duration.minutes();
        const seconds = duration.seconds();
        setRemainingTime(
          `${days} days, ${hours} hours, ${minutes} minutes, ${seconds} seconds`,
        );
      }
    };

    calculateRemainingTime();
    const intervalId = setInterval(calculateRemainingTime, 1000); // Update every second

    return () => clearInterval(intervalId);
  }, [data]);

  useEffect(() => {
    getData();
  }, []);

  async function getData() {
    try {
      const response = await axiosGet(
        `/analysis/getLog?answerId=${answerId}&node=${node?.nodeTitle}`,
      );
      const userDataResponse = response.data.data;

      setData(userDataResponse);
    } catch (err: any) {
      console.log(err);
    } finally {
      setLoader(false);
    }
  }

  return (
    <div className="createbot-modal createbot-new-background modal-scroll analytics-email-modal">
      <div className={`call-api-container`}>
        {loader ? (
          <SmallLoader />
        ) : (
          <>
            <div
              className="call-api-close close"
              onClick={() => setShowModal(false)}
            >
              <svg
                width="10"
                height="10"
                viewBox="0 0 10 10"
                fill="#6D6D6D"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                  fill="#6D6D6D"
                />
              </svg>
            </div>
            <div className="call-api-header">
              <h2>{node.nodeTitle} Details</h2>
              <p>Elit donec nisi mollis platea enim, suspendisse</p>
            </div>

            <div className="analytics-modal-body">
              {data?.status == "delivered" && (
                <div className="status-bar">
                  <div className="status-card">
                    <span className="sent">Sent</span>
                    {/* <AnalyzeCalenderIcon /> */}
                    <span>
                      {moment(data?.createdAt).format("DD MMM YYYY, HH:MM A")}
                    </span>
                  </div>
                  {data?.isOpened && (
                    <div className="status-card">
                      <span className="bounced">Viewed</span>
                      <span>
                        {moment(data?.updatedAt).format("DD MMM YYYY, HH:MM A")}
                      </span>
                    </div>
                  )}
                  {!!data?.linkClicked && (
                    <div className="status-card">
                      <span className="clicked">link Clicked</span>
                      <span>
                        {moment(data?.updatedAt).format("DD MMM YYYY, HH:MM A")}
                      </span>
                    </div>
                  )}
                  {/*
                <div className="status-card">
                <span className="unsubscribed">Unsubscribed</span>
                <span>18 Aug 2021, 12:30 PM</span>
              </div> */}
                </div>
              )}

              <div className="analytics-modal-body-inner">
                <div className="sent email">
                  <div className="email-header">
                    <SentEmailIcon />
                    <span>Sent {node?.nodeTitle}</span>
                  </div>
                  <div className="email-body">
                    {["sendEmail", "sendSMS"].some((substring) =>
                      node?.nodeTitle?.includes(substring),
                    ) ? (
                      <div
                        className="email-text grey-scrollbar"
                        dangerouslySetInnerHTML={{
                          __html:
                            data?.body?.replace(
                              new RegExp(
                                `<img src='${Environment().SERVER_API_URL}\/livebot\/updateAnalyticData\?.*?<\/img>`,
                                "gm",
                              ),
                              "",
                            ) ?? "Empty Body",
                        }}
                      ></div>
                    ) : ["segment", "condition", "abTest", "assignees"].some(
                        (substring) => node?.nodeTitle?.includes(substring),
                      ) ? (
                      <textarea
                        className="email-text grey-scrollbar"
                        style={{ width: 950, height: 299 }}
                        value={data?.data ?? "No Data"}
                      ></textarea>
                    ) : (
                      <p className="email-text grey-scrollbar">
                        {node?.nodeTitle?.includes("delayAction")
                          ? `Time Remaining: ${remainingTime}`
                          : data?.status}
                        {node?.nodeTitle?.includes("linkedIn") && (
                          <p>
                            {`Name: ${data?.name}`}
                            <br />
                            {`Email: ${data?.email}`}
                            <br />
                            {`Profile ID/ Profile URN: ${data?.profileId}`}
                            <br />
                            {data?.nodeType == "message" &&
                              `Message:  ${data?.message}`}
                            <br />
                          </p>
                        )}
                      </p>
                    )}
                  </div>
                </div>
                <div className="response email">
                  <div className="email-header">
                    <ResponseEmailIcon />
                    <span>MetaData {node?.nodeTitle}</span>
                  </div>
                  <div className="email-body">
                    {/* <textarea
                        className="email-text grey-scrollbar"
                        style={{ width: 950, height: 299 }}
                        value={data?.data ?? "No Response"}
                      ></textarea> */}

                    <p className="email-text grey-scrollbar">
                      {node?.nodeTitle?.includes("linkedIn") && (
                        <p>
                          {`Type: ${data?.nodeType}`}
                          <br />
                        </p>
                      )}
                      status: {data?.status}
                      <br />
                      statusCode: {data?.statusCode ?? ""}
                      <br />
                      {["sendEmail", "sendSMS"].some((substring) =>
                        node?.nodeTitle?.includes(substring),
                      ) && (
                        <p>
                          {`from: ${data?.from}`}
                          <br />
                          {`to: ${data?.to}`}
                          <br />
                          {`reply to: ${data?.replyTo}`}
                          <br />
                        </p>
                      )}
                      {data?.response && (
                        <textarea
                          className="email-text grey-scrollbar"
                          // style={{ width: 950, height: 299 }}
                          value={data?.response ?? "No Data"}
                        ></textarea>
                      )}
                      {data?.data && (
                        <textarea
                          className="email-text grey-scrollbar"
                          value={data?.data ?? "No Data"}
                        ></textarea>
                      )}
                      <br />
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <div className="call-api-footer">
              <button className="save-btn" onClick={() => setShowModal(false)}>
                Close
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default AnalyticsDetailedModal;
